import React from 'react';
import { Route, Switch } from 'react-router-dom';
import Header from './Header';
import Home from './Home';
import Catalog from './Catalog';
import NotFoundPage from './NotFoundPage';
import PhonePage from './PhonePage';
import Cart from './Cart';

import './App.css';

class App extends React.Component {
  constructor() {
    super();

    this.state = {
      searchStr: '',
      cart: [],
      inCart: [],
    }
  }

  handleSearch = (newSearch) => {
    this.setState({searchStr: newSearch});
  }

  buyNowHandler = (phone) => {
    let existIndex = -1;
    let newCart = [...this.state.cart];

    for (let i = 0; i < this.state.cart.length; i++) {
      if (this.state.cart[i].phone.id === phone.id) {
        existIndex = i;
        break;
      }
    }

    if (existIndex >= 0) {
      newCart[existIndex].quantity = newCart[existIndex].quantity + 1;
      this.setState((prevState) => ({
        cart: [...newCart],
        inCart: [
          ...prevState.inCart,
          phone.id
        ],
      }));
    } else {
      const id = this.state.cart[0]
        ? this.state.cart[this.state.cart.length - 1].id + 1
        : 1;
      const quantity = 1;
      this.setState((prevState) => ({
        cart: [
          ...prevState.cart,
          {
            id,
            quantity,
            phone: {...phone},
          }
        ],
        inCart: [
          ...prevState.inCart,
          phone.id,
        ],
      }));
    }
  }

  handlerCartQttySet = (index, value) => {
    const newValue = value >= 0 ? value : 0;
    const newCart = [...this.state.cart];

    if (this.state.cart[index].quantity < value) {
      this.setState(prevState => ({
        inCart: [
          ...prevState.inCart,
          this.state.cart[index].phone.id,
        ]
      }))
    }

    newCart[index].quantity = newValue;

    this.setState({
      cart: [...newCart],
    });
  }

  render() {
    return (
      <div className="App">
        <Header
          searchStr={this.state.searchStr}
          handleSearch={this.handleSearch}
          cart={this.state.cart}
        />

        <main>
          {this.state.searchStr !== ''
          ? (<>
            <Route path='/phones/:phone' component={PhonePage} />
            <Route path='/cart' exact
              render={() => <Cart cart={this.state.cart}/>}
            />
            <Catalog
              searchStr={this.state.searchStr}
              buyNowHandler={this.buyNowHandler}
              inCart={this.state.inCart}
            />
          </>)
          : (
          <Switch>
            <Route path='/' exact component={Home}/>
            <Route path='/cart' exact
              render={() => <Cart cart={this.state.cart}/>}
            />
            <Route
              path='/phones' exact
              render={() => <Catalog
                buyNowHandler={this.buyNowHandler}
                inCart={this.state.inCart}
              />}
            />
            <Route path='/phones/:phone?' component={PhonePage} />
            <Route path="*" component={NotFoundPage} />
          </Switch>)
          }
        </main>


      </div>
    )
  }
}

export default App;
