import React from 'react';
import { Link } from 'react-router-dom';

const PhoneArticle = ({ phone, buyNowHandler, inCart }) => (
  <article key={phone.age} className="article">
    <Link to={`/phones/${phone.id}`}>
        <img
          className="articleImage"
          src={`https://mate-academy.github.io/phone-catalogue-static/${phone.imageUrl}`}
          att={`Foto of ${phone.name}`}
        />
    </Link>

    <div className="articleText">
      <div className="articleHeader">
        <Link to={`/phones/${phone.id}`}>
          <p>{phone.name}</p>
        </Link>

        {inCart.includes(phone.id)
          ? <div className="buyNowBTN more" onClick={() => buyNowHandler(phone)}>Buy More</div>
          : <div className="buyNowBTN" onClick={() => buyNowHandler(phone)}>Buy Now</div>
        }

      </div>

      <div>
        {phone.snippet}
      </div>
    </div>
  </article>
);

export default PhoneArticle;
