import React from 'react';
import getDataJson from './getDataJson';
import PhoneArticle from './PhoneArticle';

class Catalog extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      phones: [],
    }
  }

  componentWillMount = async() => {
    const phones = await getDataJson('https://mate-academy.github.io/phone-catalogue-static/api/phones.json');

    this.setState({
      phones,
    });
  }

  render() {
    const { phones } = this.state;
    let filteredPhones = this.props.searchStr
      ? phones.filter(phone => phone.name.toLowerCase().includes(this.props.searchStr))
      : [...phones];

    return (
      <div className="phoneCatalog">
        {filteredPhones.map(phone => (
          <PhoneArticle
            key={phone.age}
            phone={phone}
            buyNowHandler={this.props.buyNowHandler}
            inCart={this.props.inCart}
          />
        ))}
      </div>
    );
  };
}

export default Catalog;
