import React from 'react';
import { Link } from 'react-router-dom';

const Cart = ({ cart }) => {
  if (!cart[0]) {
    return (<h2>Nothing in the Cart for Now</h2>);
  }
  return (
    <div>
      <h2>Your current order</h2>
      <table>
        <thead>
          <tr>
            <th>№</th>
            <th></th>
            <th>Item</th>
            <th>Quantity</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {cart.map(order => (
            <tr>
              <td>{order.id}</td>
              <td>
                <Link to={`/phones/${order.phone.id}`}>
                  <img
                    className="articleImage"
                    src={`https://mate-academy.github.io/phone-catalogue-static/${order.phone.imageUrl}`}
                    att={`Foto of ${order.phone.name}`}
                  />
                </Link>
              </td>
              <td>
                <Link to={`/phones/${order.phone.id}`}>
                  <p>{order.phone.name}</p>
                </Link>
              </td>
              <td><input type="number" value={order.quantity} /></td>
              <td></td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default Cart;
